<template>
  <div class="shopping">
    <navigation-bar
      :pageName="'购物车'"
      :isDefault="true"
      :isShowBack="false"
    ></navigation-bar>

    <div class="shopping-content">
      <div class="shopping-content-list">
        <div
          class="shopping-content-list-item"
          v-for="(item, index) in ShoppingData"
          :key="index"
        >
          <img
            class="shopping-content-list-item-check"
            :src="checkIcon(item.isCheck)"
            @click="onCheckClick(item)"
          />
          <img class="shopping-content-list-item-img" :src="item.img" />

          <div class="shopping-content-list-item-desc">
            <p
              class="shopping-content-list-item-desc-name"
              :class="{
                'shopping-content-list-item-desc-name-hint': !item.isHave,
              }"
            >
              <direct v-if="item.isDirect"></direct>
              <no-have v-if="!item.isHave"></no-have>
              {{ item.name }}
            </p>
            <div class="shopping-content-list-item-desc-data">
              <p class="shopping-content-list-item-desc-data-price">
                ￥{{ item.price }}
              </p>
              <goods-number-manager
                @onNumberChange="onNumberChange($event, item)"
              ></goods-number-manager>
            </div>
          </div>
        </div>
      </div>
    </div>

    <div class="shopping-content-total">
      <div class="shopping-content-total-check">
        <img :src="checkIcon(totalDatas.isAll)" @click="onCheckAllClick()" />
        <p>全选</p>
      </div>

      <div class="shopping-content-total-price">
        <p class="shopping-content-total-price-total">
          合计：<span>￥{{ totalDatas.totalPrice }}</span>
        </p>
        <p class="shopping-content-total-price-all">
          总额:<span>￥{{ totalDatas.totalPrice }} </span>
          &nbsp;&nbsp;立减:<span>￥00.00</span>
        </p>
      </div>

      <div class="shopping-content-total-settlement">
        去结算({{ totalDatas.goodsNumber }})
      </div>
    </div>
  </div>
</template>

<script>
import NavigationBar from "@c/currency/NavigationBar.vue";
import direct from "@c/goods/Direct";
import NoHave from "@c/goods/NoHave";
import GoodsNumberManager from "@c/goods/GoodsNumberManager";
export default {
  name: "ShoppingComponents",
  components: {
    NavigationBar,
    NoHave,
    direct,
    GoodsNumberManager,
  },
  data() {
    return {
      ShoppingData: [
        {
          detailImgs: [
            "http://imooc.res.lgdsunday.club/goods-detail-1-1.jpg",
            "http://imooc.res.lgdsunday.club/goods-detail-1-2.jpg",
            "http://imooc.res.lgdsunday.club/goods-detail-1-3.jpg",
            "http://imooc.res.lgdsunday.club/goods-detail-1-4.jpg",
            "http://imooc.res.lgdsunday.club/goods-detail-1-5.jpg",
            "http://imooc.res.lgdsunday.club/goods-detail-1-6.jpg",
          ],
          swiperImgs: [
            "http://imooc.res.lgdsunday.club/goods-swiper-1-1.webp.jpg",
            "http://imooc.res.lgdsunday.club/goods-swiper-1-2.webp.jpg",
            "http://imooc.res.lgdsunday.club/goods-swiper-1-3.webp.jpg",
            "http://imooc.res.lgdsunday.club/goods-swiper-1-4.webp.jpg",
          ],
          img: "http://imooc.res.lgdsunday.club/goods-1.jpg",
          isDirect: false,
          isHave: true,
          name: "【二手95新】劳力士 日志型系列 奢侈品18K白金男腕表 自动机械男士手表 116264 白色表盘",
          price: "38000",
          volume: "143",
        },
        {
          detailImgs: [
            "http://imooc.res.lgdsunday.club/goods-detail-3-1.jpg",
            "http://imooc.res.lgdsunday.club/goods-detail-3-2.jpg",
            "http://imooc.res.lgdsunday.club/goods-detail-3-3.jpg",
            "http://imooc.res.lgdsunday.club/goods-detail-3-4.jpg",
            "http://imooc.res.lgdsunday.club/goods-detail-3-5.jpg",
            "http://imooc.res.lgdsunday.club/goods-detail-3-6.jpg",
            "http://imooc.res.lgdsunday.club/goods-detail-3-7.jpg",
            "http://imooc.res.lgdsunday.club/goods-detail-3-8.jpg",
            "http://imooc.res.lgdsunday.club/goods-detail-3-9.jpg",
          ],
          swiperImgs: [
            "http://imooc.res.lgdsunday.club/goods-swiper-3-1.webp.jpg",
            "http://imooc.res.lgdsunday.club/goods-swiper-3-2.webp.jpg",
            "http://imooc.res.lgdsunday.club/goods-swiper-3-3.webp.jpg",
            "http://imooc.res.lgdsunday.club/goods-swiper-3-4.webp.jpg",
          ],
          img: "http://imooc.res.lgdsunday.club/goods-3.jpg",
          isDirect: false,
          isHave: true,
          name: "坚果（JmGO）G7 家庭投影仪 投影机家用（1080P全高清 四方梯形校正 16GB存储 全玻璃镜头）",
          price: "2698",
          volume: "4876",
        },
      ],
      totalDatas: {
        isAll: false,
        totalPrice: 0,
        goodsNumber: 0,
      },
    };
  },
  created() {
    this.ShoppingData = this.$store.state.shoppingData;
  },
  methods: {
    // 商品是否选中的点击事件
    onCheckClick(item) {
      item.isCheck = !item.isCheck;
      this.computeGoodsTotal();
    },

    //返回check图标
    checkIcon(check) {
      return check ? require("@img/check.svg") : require("@img/no-check.svg");
    },

    // 全选按钮
    onCheckAllClick() {
      this.totalDatas.isAll = !this.totalDatas.isAll;
      this.ShoppingData.forEach(
        (item) => (item.isCheck = this.totalDatas.isAll)
      );
      this.computeGoodsTotal();
    },

    // 监听选中变化
    computeGoodsTotal() {
      this.totalDatas = {
        isAll: true,
        totalPrice: 0,
        goodsNumber: 0,
      };
      this.ShoppingData.forEach((item) => {
        if (!item.isCheck) {
          this.totalDatas.isAll = false;
        } else {
          if (item.numbers) {
            this.totalDatas.goodsNumber += parseInt(item.numbers);
            this.totalDatas.totalPrice +=
              parseFloat(item.price) * parseInt(item.numbers);
          } else {
            this.totalDatas.goodsNumber++;
            this.totalDatas.totalPrice += parseFloat(item.price);
          }
        }
      });
    },

    // 监听商品数量的变化，接收数量，商品item信息和index，用arguments接收
    onNumberChange: function ($event, item) {
      let numbers = $event;
      this.ShoppingData.forEach((date) => {
        if (date === item) {
          date.numbers = numbers;
        }
      });

      if (item.isCheck) {
        this.computeGoodsTotal();
      }
    },
  },
};
</script>

<!-- .shopping {
  width: 100%;
  height: 100%;
  background-color: $bgColor;
  font-size: 20px;

  overflow: hidden;
  overflow-y: auto;
} -->

<style lang="scss" scoped>
@import "@css/style.scss";

.shopping {
  height: 100%;
  overflow: hidden;
  display: flex;
  flex-flow: column;

  .shopping-content {
    background-color: $bgColor;
    border-top: px2rem(1) solid $lineColor;
    height: 100%;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;

    &-list {
      height: 100%;
      overflow: hidden;
      overflow-y: auto;

      &-item {
        background-color: white;
        padding: $marginSize;
        display: flex;
        border-bottom: px2rem(1) solid $lineColor;

        &-check {
          width: $checkSize;
          align-self: center;
          padding: px2rem(6);
        }

        &-img {
          width: px2rem(100);
          height: px2rem(100);
        }

        &-desc {
          width: 100%;
          display: flex;
          flex-direction: column;
          justify-content: space-between;
          padding: 0 $marginSize;

          &-name {
            font-size: $infoSize;
            overflow: hidden;
            text-overflow: ellipsis;
            word-break: break-word;
            -webkit-line-clamp: 2;
            -webkit-box-orient: vertical;
            display: -webkit-box;
            line-height: px2rem(18);

            &-hint {
              color: $textHintColor;
            }
          }

          &-data {
            width: 100%;
            display: flex;
            align-items: center;
            justify-content: space-between;
            margin-top: $marginSize;

            &-price {
              font-size: $titleSize;
              color: $mainColor;
              font-weight: 500;
            }
          }
        }
      }
    }

    &-total {
      height: px2rem(56);
      box-sizing: border-box;
      display: flex;
      align-items: center;
      background-color: white;
      border-top: px2rem(1) solid $lineColor;

      &-check {
        display: flex;
        align-items: center;
        margin-left: $marginSize;
        margin-right: $marginSize;
        img {
          width: $checkSize;
          padding: px2rem(6);
        }

        p {
          font-size: $infoSize;
        }
      }

      &-price {
        flex-grow: 2;
        display: flex;
        flex-direction: column;
        &-total {
          font-size: $titleSize;
          margin-bottom: px2rem(6);
          span {
            font-weight: bold;
          }
        }

        &-all {
          font-size: $minInfoSize;
          span {
            font-weight: bold;
          }
        }
      }

      &-settlement {
        width: px2rem(120);
        height: 100%;
        font-size: $titleSize;
        background-color: $mainColor;
        color: white;
        display: flex;
        align-items: center;
        justify-content: center;
      }
    }
  }
}
</style>
